img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../../fonts/Raleway-ExtraBold.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/Rubik-Regular.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../../fonts/Rubik-Bold.woff") format("woff");
}
.button {
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 800;
  text-align: center;
  color: #000000;
  background-color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
}
.page {
  height: 100%;
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
}
.page__main {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  max-width: 1440px;
  margin: 0 auto;
  background-color: #ffffff;
  color: #444444;
}
.page__header {
  background-color: #ffe17f;
  color: #000000;
}
.navigation__link {
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
.navigation__user a {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}
.title {
  margin: 0;
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 50px;
  color: #000000;
}
.category__link {
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 24px;
  color: #000000;
}
.characteristic__indicator {
  color: #000000;
  font-size: 36px;
}
.characteristic__descr {
  margin: 0;
  font-size: 16px;
}
.about__item {
  color: #000000;
  font-weight: 700;
  line-height: 20px;
}
.about__descr {
  margin: 0;
}
.contacts__subtitle {
  margin: 0;
  font-weight: 700;
  font-size: 24px;
}
.contacts__address {
  font-style: normal;
}
.contacts__phone {
  color: #444444;
  font-style: normal;
}
.breadcrumbs {
  color: #444444;
  font-size: 16px;
  line-height: 19px;
}
.breadcrumbs__link {
  color: inherit;
}
.catalog-filter__title {
  font-weight: 700;
  line-height: 20px;
}
.control__label {
  font-size: 16px;
  line-height: 19px;
}
.sorting-container {
  background-color: #ebebeb;
}
.sorting-type__label {
  font-family: 'Raleway';
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
}
.pagination {
  background-color: #ebebeb;
}
.pagination__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #444444;
  opacity: 0.3;
}
.pagination__next,
.pagination__prev {
  font-family: 'Raleway';
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
  align-items: center;
  text-transform: uppercase;
  color: #000000;
  opacity: 1;
}
.product-card__link {
  color: #000000;
}
.product-card__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}
.product-card__price {
  line-height: 21px;
  color: #444444;
}
.product-card__show-more {
  font-size: 16px;
  line-height: 19px;
}
.footer {
  background-color: #444444;
}
.footer__address {
  font-style: normal;
  color: #ffffff;
  font-size: 16px;
  line-height: 19px;
}
.footer__phone {
  font-style: normal;
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
}
.info__link {
  font-family: "Raleway";
  color: #ffffff;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
